import * as React from "react"
import { RenderEnvironment, RenderTarget } from "../render/types/RenderEnvironment"
import { Frame, FrameProps } from "./Frame"
import { Size } from "../render/types/Size"

export interface Props {
    // tslint:disable:react-unused-props-and-state
    children: React.ReactNode
    size: Size
    title?: string
    hide?: boolean
    showArrow?: boolean
}

export function EmptyState({ title = "Connect to content", children, size, hide, showArrow = true }: Props) {
    const { target } = RenderEnvironment
    if (target !== RenderTarget.canvas) return null
    if (hide) return null
    const childCount = React.Children.count(children)
    if (childCount !== 0) return null

    const width = size.width
    const height = size.height

    if (width < 0 || height < 0) return null

    // Determine when the frame is too small to show the text/arrow
    const minHeight = 24
    const arrowWidth = 28
    const hasAvailableHeight = height >= minHeight

    const shouldShowArrow = showArrow && hasAvailableHeight && width >= arrowWidth + 6
    const shouldShowTitle = hasAvailableHeight && (!showArrow || shouldShowArrow)

    return (
        <Frame
            key={`empty-state`}
            top={0}
            left={0}
            width={width}
            height={height}
            className="framerInternalUI-canvasPlaceholder"
            style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                lineHeight: "1",
                padding: "0 10px",
            }}
        >
            {shouldShowTitle && <Title>{title}</Title>}
            {shouldShowArrow && <Arrow />}
        </Frame>
    )
}

const scaleFactor = "var(--framerInternalCanvas-canvasPlaceholderContentScaleFactor, 1)"

const Title: React.SFC = ({ children }) => {
    return (
        <span
            style={{
                flex: "auto",
                whiteSpace: "nowrap",
                overflow: "hidden",
                textAlign: "center",
                // Use a mask to fade out the right edge of the text as it moves under the arrow.
                WebkitMaskImage: `linear-gradient(90deg, black, black calc(100% - 12px * ${scaleFactor}), transparent)`,
            }}
        >
            {children}
        </span>
    )
}
const Arrow: React.SFC = () => {
    const height = 7
    const width = 14
    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            width={width}
            height={height}
            viewBox={`0 0 ${width} ${height}`}
            style={{
                width: width,
                opacity: 0.9,
                transform: `scale(${scaleFactor})`,
                transformOrigin: "100% 50%",
                marginTop: 1,
            }}
        >
            <g transform="translate(0.5 0.5)">
                <path d="M 0 3 L 12 3" fill="transparent" stroke="currentColor" strokeLinecap="butt" />
                <path d="M 9 0 L 12 3 L 9 6" fill="transparent" stroke="currentColor" strokeLinecap="butt" />
            </g>
        </svg>
    )
}
