import { deprecationWarning } from "../../utils/deprecation"
import { isFiniteNumber } from "../utils/isFiniteNumber"

/** @public */
export type PropertyControls<ComponentProps = any, ArrayTypes = any> = {
    [K in keyof ComponentProps]?: ControlDescription<Partial<ComponentProps>, ArrayTypes>
}

export type CompactControlsDescription<P = any> = NumberControlDescription<P> | EnumControlDescription<P>

export type FlatControlDescription<P = any> =
    | CompactControlsDescription<P>
    | BooleanControlDescription<P>
    | StringControlDescription<P>
    | ColorControlDescription<P>
    | FusedNumberControlDescription<P>
    | SegmentedEnumControlDescription<P>
    | ImageControlDescription<P>
    | FileControlDescription<P>
    | ComponentInstanceDescription<P>

/** @public */
export type ControlDescription<P = any, Q = any> = FlatControlDescription<P> | ArrayControlDescription<P, Q>

/**
 * Used by the {@link PropertyControls} and specifies the type of user
 * interface for receiving input.
 *
 * @remarks
 * Each field has a distinct set of properties though they all accept `title`
 * and `hidden` properties.
 *
 * ```typescript
 * static propertyControls: PropertyControls<Props> = {
 *   text: {
 *     type: ControlType.String,
 *     title: "Text",
 *     hidden: (props) => true  // Return true to hide, receives component props
 *   },
 * }
 * ```
 * @public
 */
export const enum ControlType {
    /**
     * A control that displays an on/off checkbox, the associated prop will be true/false
     * depending on the state of the checkbox.
     * @remarks
     * ```typescript
     * static propertyControls: PropertyControls<Props> = {
     *   isCentered: {
     *     type: ControlType.Boolean,
     *     title: "Center",
     *     defaultValue: true, // Optional default state
     *     disabledTitle: "Not Centered", // Optional title when disabled
     *     enabledTitle?: "Centered", // Optional title when enabled
     *   },
     * }
     * ```
     */
    Boolean = "boolean",
    /**
     * A control that accepts a number. This will be provided directly as a prop.
     * @remarks
     * Will display an input field with a range slider by default. The
     * `displayStepper` option can be provided to include a stepper.
     * ```typescript
     * static propertyControls: PropertyControls<Props> = {
     *   offset: {
     *     type: ControlType.Number,
     *     defaultValue: 10, // Optional default value, blank otherwise
     *     max: 1, // Optional upper bound for the input
     *     min: 0, // Optional lower bound for the input
     *     unit: "px", // Optional suffix e.g. "px" to be dispayed next to the value
     *     step: 0.1, // Optional step for the stepper to increment.
     *     displayStepper: true, // If true stepper will be added to input.
     *   },
     * }
     * ```
     */
    Number = "number",
    /**
     * A control that accepts plain text. This will be provided directly as a prop.
     * @remarks
     * Will display an input field with optional placeholder.
     * ```typescript
     * static propertyControls: PropertyControls<Props> = {
     *   offset: {
     *     type: ControlType.String,
     *     defaultValue: "Hello World", // Optional default value, blank otherwise
     *     placeholder: "Enter some text", // Optional placeholder text for empty input
     *   },
     * }
     * ```
     */
    String = "string",
    /**
     * A control that can be used to take a single number or four distinct fields. These
     * will be mapped across six associated props.
     * @remarks
     * The canonical example for this control is for border, padding or margin control.
     * It will display an input field to accept a single value and a segmented control
     * allowing values to be provided for four distinct fields. A boolean prop can be
     * used in code to determine whether the single or multi input field was used.
     *
     * ```typescript
     * interface Props {
     *   isPerSide: boolean
     *   border: number
     *   top: number
     *   left: number
     *   right: number
     *   bottom: number
     * }
     * static propertyControls: PropertyControls<Props> = {
     *   title: {
     *     type: ControlType.FusedNumber,
     *     // Optional default value for each field.
     *     defaultValue: 0,
     *     // The prop that determines whether the single or multi value input was used.
     *     toggleKey: "isPerSide",
     *     // The individual props to map the values to.
     *     valueKeys: ["top", "left", "right", "bottom"],
     *     // Labels for each of the fields.
     *     valueLabels: ["T", "L", "R", "B"],
     *     // Optional minimum value for each field.
     *     min: 0,
     *   },
     * }
     * ```
     */
    FusedNumber = "fusednumber",
    /**
     * Represents a list of options, the selected option will be provided as a prop.
     * @remarks
     * This control is displayed as a pop-up menu with one of the items in the list
     * selectable.
     *
     * ```typescript
     * interface Props {
     *   value: "a" | "b" | "c"
     * }
     * static propertyControls: PropertyControls<Props> = {
     *   value: {
     *     type: ControlType.Enum,
     *     defaultValue: "a", // Optional selected value
     *     options: ["a", "b", "c"], // The available options
     *     optionTitles: ["Option A", "Option B", "Option C"], Optional human readable titles
     *   },
     * }
     * ```
     */
    Enum = "enum",
    /**
     * Represents a list of options, the selected option will be provided as a prop.
     * @remarks
     * This control is displayed as a segmented control, otherwise exactly the
     * same as {@link ControlType.Enum}.
     *
     * ```typescript
     * interface Props {
     *   value: "a" | "b" | "c"
     * }
     * static propertyControls: PropertyControls<Props> = {
     *   value: {
     *     type: ControlType.SegmentedEnum,
     *     defaultValue: "a", // Optional selected value
     *     options: ["a", "b", "c"], // The available options
     *     optionTitles: ["Option A", "Option B", "Option C"], Optional human readable titles
     *   },
     * }
     * ```
     */
    SegmentedEnum = "segmentedenum",
    /**
     * Represents a color value, will be provided as a string via props.
     * @remarks
     * This control is displayed as a color picker and will provide the selected color in either
     * hex (e.g. `"#afafaf"`) or hsl (e.g. `hsla(203, 87%, 50%, 0.43)`) notation depending on
     * whether there is a alpha channel.
     *
     * ```typescript
     * interface Props {
     *   background: string
     * }
     * static propertyControls: PropertyControls<Props> = {
     *   background: {
     *     type: ControlType.Color,
     *     defaultValue: "#ffffff", // Optional default color
     *   },
     * }
     * ```
     */
    Color = "color",
    /**
     * Represents an image resource, will be provided as url via props.
     * @remarks
     * Displayed as an image well with associated file picker. The chosen asset
     * will be provided as a fully qualified url.
     *
     * ```typescript
     * interface Props {
     *   avatar: string
     * }
     * static propertyControls: PropertyControls<Props> = {
     *   avatar: {
     *     type: ControlType.Image,
     *   },
     * }
     * ```
     */
    Image = "image",
    /**
     * Represents an image resource, will be provided as url via props.
     * @remarks
     * Displayed as an file picker that will open a native file browser. The
     * selected file will be provided as a fully qualified url. The
     * `allowedFileTypes` property must be provided to specify acceptable file
     * types.
     *
     * ```typescript
     * interface Props {
     *   asset: string
     * }
     * static propertyControls: PropertyControls<Props> = {
     *   asset: {
     *     type: ControlType.File,
     *     allowedFileTypes: ["pdf"], // Accepted file types
     *   },
     * }
     * ```
     */
    File = "file",
    /**
     * A reference to another component on the canvas, provided via a prop.
     * @remarks
     * The component will have an outlet to allow dragging a link to another
     * Frame. Available frames will also be displayed in a pop-up in the
     * properties panel. The component reference will be provided as a prop,
     * as a convention the name for the prop is usually just `children`.
     *
     * Multiple components can be linked by combining the `ComponentInstance`
     * type with the {@link ControlType.Array}.
     *
     * ```typescript
     * interface Props {
     *   children: React.ReactNode
     * }
     * static propertyControls: PropertyControls<Props> = {
     *   children: {
     *     type: ControlType.ComponentInstance,
     *   },
     * }
     * ```
     */
    ComponentInstance = "componentinstance",
    /**
     * Allows multiple values per ControlType, provided as an array via props.
     * @remarks
     * For most control types this control will be displayed as an additional
     * section within the properties panel allowing as many fields to be provided
     * as required.
     *
     * For a {@link ControlType.ComponentInstance} the Frame will also gain
     * an additional outlet UI that allows links to be dragged between frames.
     *
     * ```typescript
     * interface Props {
     *   images: string[],
     *   children: React.ReactNode[]
     * }
     * static propertyControls: PropertyControls<Props> = {
     *   images: {
     *     type: ControlType.Array,
     *     propertyControl: {
     *       type: ControlType.Image
     *     }
     *   },
     *   children: {
     *     type: ControlType.Array,
     *     propertyControl: {
     *       type: ControlType.ComponentInstance
     *     }
     *   },
     * }
     * ```
     */
    Array = "array",
}

const ControlTypeNames = {
    boolean: true,
    number: true,
    string: true,
    fusednumber: true,
    enum: true,
    segmentedenum: true,
    color: true,
    image: true,
    file: true,
    componentinstance: true,
    array: true,
}

function isControlType(name: string): name is ControlType {
    return ControlTypeNames[name]
}

export interface BaseControlDescription<P = any> {
    title?: string
    hidden?(props: P): boolean
}

export interface BooleanControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.Boolean
    defaultValue?: boolean
    disabledTitle?: string
    enabledTitle?: string
}

export interface NumberControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.Number
    defaultValue?: number
    max?: number
    min?: number
    unit?: string
    step?: number
    displayStepper?: boolean
}

export interface StringControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.String
    defaultValue?: string
    placeholder?: string
}

export interface FusedNumberControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.FusedNumber
    defaultValue?: number
    toggleKey: keyof P
    toggleTitles: [string, string]
    valueKeys: [keyof P, keyof P, keyof P, keyof P]
    valueLabels: [string, string, string, string]
    min?: number
}

export interface DeprecatedFusedNumberControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.FusedNumber
    valueKeys: [keyof P, keyof P, keyof P, keyof P]
    valueLabels: [string, string, string, string]
    min?: number
    // deprecated
    splitKey: keyof P
    splitLabels: [string, string]
}

export interface EnumControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.Enum
    defaultValue?: string
    options: string[]
    optionTitles?: string[] | ((props: P | null) => string[])
}

export interface SegmentedEnumControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.SegmentedEnum
    defaultValue?: string
    options: string[]
    optionTitles?: string[] | ((props: P | null) => string[])
}

export interface ColorControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.Color
    defaultValue?: string
}

export interface ImageControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.Image
}

export interface FileControlDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.File
    allowedFileTypes: string[]
}

export interface ComponentInstanceDescription<P = any> extends BaseControlDescription<P> {
    type: ControlType.ComponentInstance
}

/** @public */
export interface ArrayControlDescription<P = any, Q = any> extends BaseControlDescription<P> {
    type: ControlType.Array
    propertyControl: FlatControlDescription<Q>
    maxCount?: number
    defaultValue?: any[]
}

function toNumber(o: any, min?: number): number | undefined {
    if (typeof o === "number" && (!isFiniteNumber(min) || o >= min)) return o
}

function toString(o: any): string | undefined {
    if (typeof o === "string") return o
}

function toBoolean(o: any): boolean | undefined {
    if (o === true || o === false) return o
}

function toStringList(o: any): string[] | undefined {
    if (o instanceof Array) {
        return o.map(v => toString(v)).filter(v => v) as string[]
    }
}

function toString2(o: any): [string, string] | undefined {
    if (o instanceof Array) {
        const s1 = toString(o[0])
        const s2 = toString(o[1])
        if (s1 && s2) return [s1, s2]
    }
}

function toString4(o: any): [string, string, string, string] | undefined {
    if (o instanceof Array) {
        const s1 = toString(o[0])
        const s2 = toString(o[1])
        const s3 = toString(o[2])
        const s4 = toString(o[3])
        if (s1 && s2 && s3 && s4) return [s1, s2, s3, s4]
    }
}

function toHiddenFunction<P = any>(o: any): ((props: P) => boolean) | undefined {
    if (typeof o === "function") return o
}

function toStringArrayFunction<P = any>(o: any): ((props: P) => string[]) | undefined {
    if (typeof o === "function") return o
}

function toArray(o: any, type: ControlType): any[] | undefined {
    if (!Array.isArray(o)) return
    switch (type) {
        case ControlType.Boolean:
            return o.filter(item => item === true || item === false)
        case ControlType.Number:
            return o.filter(isFiniteNumber)
        case ControlType.String:
        case ControlType.Enum:
        case ControlType.SegmentedEnum:
        case ControlType.Color:
            return toStringList(o)
    }
}

function defaultControl(
    type: ControlType,
    values: Partial<
        BooleanControlDescription &
            NumberControlDescription &
            StringControlDescription &
            FusedNumberControlDescription &
            DeprecatedFusedNumberControlDescription &
            EnumControlDescription &
            SegmentedEnumControlDescription &
            ColorControlDescription &
            ImageControlDescription &
            FileControlDescription &
            ComponentInstanceDescription &
            ArrayControlDescription
    >
): ControlDescription | ArrayControlDescription | null {
    const title = toString(values.title)
    const hidden = toHiddenFunction(values.hidden)
    const defaultValue = values.defaultValue
    switch (type) {
        case ControlType.Boolean: {
            const disabledTitle = toString(values.disabledTitle)
            const enabledTitle = toString(values.enabledTitle)
            return { title, hidden, defaultValue: toBoolean(defaultValue), type, disabledTitle, enabledTitle }
        }
        case ControlType.Number: {
            const min = toNumber(values.min)
            const max = toNumber(values.max)
            const unit = toString(values.unit)
            const step = toNumber(values.step)
            const displayStepper = toBoolean(values.displayStepper)
            return { title, hidden, defaultValue: toNumber(defaultValue), type, min, max, unit, step, displayStepper }
        }
        case ControlType.String:
            const placeholder = toString(values.placeholder)
            return { title, hidden, defaultValue, type, placeholder }
        case ControlType.FusedNumber: {
            const valueKeys = toString4(values.valueKeys)
            const valueLabels = toString4(values.valueLabels)
            const min = toNumber(values.min)

            let toggleKey = toString(values.toggleKey)
            let toggleTitles = toString2(values.toggleTitles)

            if (!toggleKey) {
                toggleKey = toString(values.splitKey)
                if (!toggleKey) return null
                deprecationWarning("splitKey option of FusedNumber control type", "1.0.0", "toggleKey")
            }

            if (!toggleTitles) {
                toggleTitles = toString2(values.splitLabels)
                if (!toggleTitles) return null
                deprecationWarning("splitLabels option of FusedNumber control type", "1.0.0", "toggleTitles")
            }

            if (!toggleKey || !toggleTitles || !valueKeys || !valueLabels) return null
            return {
                title,
                hidden,
                defaultValue: toNumber(defaultValue),
                type,
                toggleKey,
                toggleTitles,
                valueKeys,
                valueLabels,
                min,
            }
        }
        case ControlType.Enum: {
            const options = toStringList(values.options)
            if (!options) return null
            const optionTitles = toStringList(values.optionTitles) || toStringArrayFunction(values.optionTitles)
            return { title, hidden, defaultValue: toString(defaultValue), type, options, optionTitles }
        }
        case ControlType.SegmentedEnum: {
            const options = toStringList(values.options)
            if (!options) return null
            const optionTitles = toStringList(values.optionTitles) || toStringArrayFunction(values.optionTitles)
            return { title, hidden, defaultValue: toString(defaultValue), type, options, optionTitles }
        }
        case ControlType.Color:
            return { title, hidden, defaultValue: toString(defaultValue), type }
        case ControlType.Image:
            return { title, hidden, type }
        case ControlType.File:
            const allowedFileTypes = toStringList(values.allowedFileTypes)
            if (!allowedFileTypes) return null
            return { title, hidden, type, allowedFileTypes }
        case ControlType.ComponentInstance:
            return { title, hidden, type }
        case ControlType.Array:
            if (!values.propertyControl || !values.propertyControl.type) return null
            const propertyControl = defaultControl(values.propertyControl.type, values.propertyControl as object) // No idea why this cast is needed
            if (!propertyControl) return null
            if (propertyControl.type === ControlType.Array) return null
            const maxCount = toNumber(values.maxCount, 1)
            return {
                title,
                hidden,
                type,
                propertyControl,
                maxCount,
                defaultValue: toArray(values.defaultValue, propertyControl.type),
            }
    }
    return null
}

export function verifyPropertyControls(desc: object): PropertyControls<any> {
    const controls: PropertyControls<any> = {}
    for (const key in desc) {
        const value = desc[key]
        if (typeof value === "string" && isControlType(value)) {
            const d = defaultControl(value, {})
            if (d) {
                controls[key] = d
            }
            continue
        }
        const type = value.type
        if (typeof type === "string" && isControlType(type)) {
            const d = defaultControl(type, value)
            if (d) {
                controls[key] = d
            }
            continue
        }
    }

    return controls
}
