import * as React from "react"
import { Frame } from "./Frame"
import { Size } from "../../render/types/Size"

/**
 * @internal
 */
export enum PlaceholderType {
    Loading,
    Error,
}

/**
 * @internal
 */
export function ComponentPlaceholder({
    type,
    size,
    title,
    message,
    props,
}: {
    type: PlaceholderType
    size: Size
    title?: string
    message?: string
    props?: any
}) {
    return (
        <Frame
            parentSize={size}
            className={className(type)}
            style={baseStyle}
            width={"100%"}
            height={"100%"}
            {...props}
        >
            <div style={titleStyle}>{placeholderTitle(type, title)}</div>
            {message && <div style={messageStyle}>{message}</div>}
        </Frame>
    )
}

const baseStyle: React.CSSProperties = {
    background: undefined,
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    lineHeight: "1.4em",
    textOverflow: "ellipsis",
    overflow: "hidden",
    minHeight: 0,
}

const textStyle: React.CSSProperties = {
    overflow: "hidden",
    whiteSpace: "nowrap",
    textOverflow: "ellipsis",
    maxWidth: "100%",
    flexShrink: 0,
    padding: `0 10px`,
}

const titleStyle: React.CSSProperties = {
    ...textStyle,
    fontWeight: 500,
}

const messageStyle: React.CSSProperties = {
    ...textStyle,
    maxHeight: "calc(50% - calc(20px * var(--framerInternalCanvas-canvasPlaceholderContentScaleFactor, 1)))",
    opacity: 0.8,
    WebkitMaskImage: "linear-gradient(to bottom, black 80%, transparent 100%)",
}

function className(type: PlaceholderType): string {
    switch (type) {
        case PlaceholderType.Error:
            return "framerInternalUI-canvasErrorPlaceholder"
        case PlaceholderType.Loading:
            return "framerInternalUI-canvasComponentPlaceholder"
    }
}

function placeholderTitle(type: PlaceholderType, title: string | undefined) {
    if (!title) return typeTitle(type)
    return `${typeTitle(type)} in ${stripSlash(title)}`
}

function typeTitle(type: PlaceholderType) {
    switch (type) {
        case PlaceholderType.Loading:
            return "Loading"
        case PlaceholderType.Error:
            return "Error"
    }
}

function stripSlash(title: string) {
    if (title.startsWith("./")) {
        return title.replace("./", "")
    }
    return title
}
